import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/goods/:type',
    name: 'goods',
    component: resolve => require(['../views/Goods.vue'], resolve),
  },
  {
    path: '/goodsDetail/:goodsId',
    name: 'goodsDetail',
    component: resolve => require(['../views/GoodsDetail.vue'], resolve),
  },
  {
    path: '/news',
    name: 'news',
    component: resolve => require(['../views/News.vue'], resolve),
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
