import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLogin:false,
    token:'',
    userName:'',
    cartNum:0,//购物车数量
  },
  mutations: {
    // 登录过期或者登出
    logout(state){
      state.isLogin = false;
      state.token = '';
      state.userName = '';
      state.cartNum = 0;
    },
    // 登录
    login(state,token,userName,num){
      state.isLogin = true;
      state.token = token;
      state.userName = userName;
      state.cartNum = num;
    },
    // 添加商品到购物车
    incrementGoods(state,num){
      state.cartNum += num;
    },
    // 从购物车删除商品
    decrementGoods(state,num){
      state.cartNum -= num;
    }
  },
  actions: {

  },
  modules: {

  }
})
